.benefits {
  @include on-tablet {
    display: none;
  }
  &__text {
    @include benefits-text-desktop-mixin;
  }
  &__text--black {
    @include benefits-text-desktop-mixin;

    font-weight: 700;
    color: #131313;
  }
  &__text-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 84px;
  }
}
